<template>
  <div class="Socila-Contacts">
    <a target="_blank" href="https://www.instagram.com/gobienan/">
      <img src="../../static/instagram.svg" alt="Instagram-Logo">
    </a>
    <a target="_blank" href="https://www.linkedin.com/in/gobie-nanthakumar/">
      <img src="../../static/linkedin.svg" alt="LinkedIn-Logo">
    </a>
  </div>
</template>

<script>
export default {
  name: "SocialContact",
  props: {
    ButtonText: {
      type: String,
      default() {
        return "";
      }
    }
  },
  components: {},
  data() {
    return {};
  },
  created() {},
  watch: {},
  computed: {},
  methods: {},

  mounted() {}
};
</script>

<style scoped lang="scss">
@import "../style/style.scss";
.Socila-Contacts {
  position: fixed;
  left: 30px;
  top: 50%;
  transform: translate(0, -50%) translateZ(0);
  z-index: zIndex(socialcontact);
  display: flex;
  flex-direction: column;
  @media (max-width: 1300px) {
    position: absolute;
    width: 100%;
    top: initial;
    left: 50%;
    bottom: 30px;
    transform: translate(-50%, 0%);
    flex-direction: row;
    justify-content: center;
  }
  @media (max-width: 500px) {
    height: 55px;
    bottom: 20px;
    z-index: zIndex(default) + 2;
  }
  a {
    transform: translateX(-60px);
    opacity: 0;
    animation-name: show;
    animation-fill-mode: forwards;
    animation-duration: 0.3s;
    &:hover {
      img {
        filter: grayscale(0%);
        opacity: 1;
      }
    }
    @for $i from 0 through 3 {
      &:nth-child(#{$i}) {
        animation-delay: calc(calc(#{$i} + 30) * 0.125s);
      }
      @media (max-width: 500px) {
        &:nth-child(#{$i}) {
          position: absolute;
          left: calc(12.5% * 2 * (#{$i}) - 55px / 2);
        }
      }
    }

    img {
      display: block;
      width: 25px;
      height: 25px;
      margin: 35px;
      filter: grayscale(100%);
      opacity: 0.75;
      transition: all 0.195s;
      pointer-events: none;
      @media (max-width: 500px) {
        width: 25px;
        height: 25px;
        margin: 15px;
      }
    }
  }
  @keyframes show {
    100% {
      opacity: 1;
      transform: rotate(0) translateX(0px);
    }
  }
}
</style>



