<template>
  <div
    class="Bg-Lines"
    :class="{ 'hasColor-ada': ada,'hasColor-taxifusion': taxifusion,'hasColor-lumencase': lumencase,'hasColor-slected': slected,'hasColor-codehoney': codehoney }"
  >
    <div class="Bg-Line Bg-Line-1"></div>
    <div class="Bg-Line Bg-Line-2">
      <div class="Bg-Line-Animation"></div>
    </div>
    <div class="Bg-Line Bg-Line-3">
      <div class="Bg-Line-Animation" v-show="bgLine"></div>
    </div>
    <div class="Bg-Line Bg-Line-4">
      <div class="Bg-Line-Animation" v-show="bgLine"></div>
    </div>
    <div class="Bg-Line Bg-Line-5">
      <div class="Bg-Line-Animation"></div>
    </div>
    <div class="Bg-Line Bg-Line-6">
      <div class="Bg-Line-Animation" v-show="bgLine"></div>
    </div>
    <div class="Bg-Line Bg-Line-7">
      <div class="Bg-Line-Animation"></div>
    </div>
    <div class="Bg-Line Bg-Line-8">
      <div class="Bg-Line-Animation" v-show="bgLine"></div>
    </div>
  </div>
</template>
<script>
export default {
  name: "BgLines",
  props: {
    ada: {
      type: Boolean,
      default() {
        return false;
      }
    },
    taxifusion: {
      type: Boolean,
      default() {
        return false;
      }
    },
    lumencase: {
      type: Boolean,
      default() {
        return false;
      }
    },
    slected: {
      type: Boolean,
      default() {
        return false;
      }
    },
    codehoney: {
      type: Boolean,
      default() {
        return false;
      }
    },
    bgLine: {
      type: Boolean,
      default() {
        return true;
      }
    }
  },
  components: {},
  data() {
    return {};
  },
  created() {},
  watch: {},
  computed: {},
  methods: {},

  mounted() {}
};
</script>
<style lang="scss">
@import "../style/style.scss";
.Bg-Lines {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
  z-index: zIndex(default) + 1;
  &.hasColor-ada {
    .Bg-Line {
      &::before {
        transform: translateX(0);
        background-color: color(ada);
        transition-delay: 0s;
      }
    }
  }
  &.hasColor-taxifusion {
    .Bg-Line {
      &::before {
        transform: translateX(0);
        background-color: color(taxifusion);
      }
    }
  }
  &.hasColor-lumencase {
    .Bg-Line {
      &::before {
        transform: translateX(0);
        background-color: color(lumencase);
      }
    }
  }
  &.hasColor-slected {
    .Bg-Line {
      &::before {
        transform: translateX(0);
        background-color: color(slected);
      }
    }
  }
  &.hasColor-codehoney {
    .Bg-Line {
      &::before {
        transform: translateX(0);
        background-color: color(second);
      }
    }
  }
}
.Bg-Line {
  position: absolute;
  top: 0;
  width: 1px;
  height: 100%;
  z-index: 1;
}
.Bg-Line {
  overflow: hidden;
  width: calc(12.5vw + 1px);

  &::before {
    position: absolute;
    top: 0;
    transform: translateX(-12.5vw);
    content: "";
    width: calc(12.5vw - 0px);
    height: 100%;
    transition: all 0.5s ease;
    transition-delay: 0.3s;
  }
  &::after {
    position: absolute;
    left: 0;
    top: 0;
    content: "";
    height: 100%;
    width: 1px;
    background-color: color(border);
  }
  .Bg-Line-Animation {
    position: absolute;
    width: 3px;
    left: -1px;
    height: 140px;
    z-index: 99;
    overflow: hidden;
    &::before {
      position: absolute;
      content: "";
      transform: translateY(-100%);
      width: 100%;
      height: 50px;
      top: 0;
      background-color: color(prime);
      animation-name: lineanimation;
      animation-duration: 4s;
      animation-iteration-count: 2;
      z-index: 99999;
    }
  }
}
.Bg-Line-2 {
  .Bg-Line-Animation {
    top: 20%;
    &::before {
      height: 80px;
    }
  }
}
.Bg-Line-3 {
  .Bg-Line-Animation {
    top: 30%;
    &::before {
      height: 150px;
      animation-delay: 1.5s;
      animation-iteration-count: infinite;
    }
  }
}
.Bg-Line-4 {
  .Bg-Line-Animation {
    top: 10%;
    &::before {
      animation-delay: 3.5s;
      animation-timing-function: ease;
      animation-iteration-count: infinite;
      height: 70px;
    }
  }
}

.Bg-Line-5 {
  .Bg-Line-Animation {
    top: 85%;
    &::before {
      animation-delay: 2s;
    }
  }
}
.Bg-Line-7 {
  .Bg-Line-Animation {
    top: 50%;
    &::before {
      height: 120px;
      animation-delay: 1s;
    }
  }
}
.Bg-Line-8 {
  .Bg-Line-Animation {
    top: 50%;
    &::before {
      animation-delay: 5s;
      animation-timing-function: ease;
      animation-iteration-count: infinite;
      height: 50px;
    }
  }
}
@keyframes lineanimation {
  100% {
    transform: translateY(500%);
  }
}
$elements: 9;
@for $i from 0 to $elements {
  .Bg-Line:nth-child(#{$i}) {
    left: calc((#{$i} - 1) * 12.5vw - 1px);
  }
}
</style>



